<template>
  <div class="list">
    <div class="clearfix list-button">
      <div class="pull-left">
        <h4 class="list-button-text">商机基本信息：</h4>
      </div>
      <div class="pull-right" style="margin-right: 10px">
        <el-button   @click="">创建合同</el-button>
        <el-button   @click="">删除</el-button>
        <el-button   @click="">保存</el-button>
      </div>
    </div>
    <div class="clearfix list-center">
      <!--<h-table></h-table>-->
      <el-col :span="12">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
          <el-form-item label="商机编号" prop="name">
            <el-input v-model="ruleForm.index"></el-input>
          </el-form-item>
          <el-form-item label="项目名称" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="渠道" prop="name">
            <el-input v-model="ruleForm.channel"></el-input>
          </el-form-item>
          <el-form-item label="商机登记时间" required>
            <el-date-picker v-model="ruleForm.date1" type="datetime" placeholder="选择日期时间">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="状态" prop="region">
            <el-select v-model="ruleForm.name"  placeholder="请选择商机状态" style="display: block">
              <el-option
                v-for="item in stateOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="销售负责人" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
          <el-form-item label="客户名称" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
          </el-form-item>
        </el-form>
      </el-col>
    </div>
    <div class="clearfix list-button">
      <div class="pull-left">
        <h4 class="list-button-text">客户联系人信息：</h4>
      </div>
    </div>
    <div class="clearfix list-center">
      <!--列表-->
      <el-table :data="tableData"  v-loading="listLoading" @selection-change="" class="chanceTable">
        <el-table-column type="index" label="联系人编号" width="100" align="center">
        </el-table-column>
        <el-table-column prop="role" label="角色" align="center" >
        </el-table-column>
        <el-table-column prop="name" label="联系人姓名" align="center"  >
        </el-table-column>
        <el-table-column prop="contactPhone" label="手机号码"  align="center"  >
        </el-table-column>
        <el-table-column prop="address" label="收件地址" align="center"  >
        </el-table-column>
        <el-table-column label="操作" width="180" align="center">
          <template scope="scope">
            <el-button size="small" @click="">查看</el-button>
            <el-button type="danger" size="small" @click="">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--工具条-->
      <el-col :span="24" class="toolbar">
        <el-col :span="20" style="height:32px">
          <el-pagination background layout="prev, pager, next,total" @current-change="" :page-size="10" :total="total">
          </el-pagination>
        </el-col>
        <el-col :span="4">
          <div class="clearfix list-button" style="margin: 0">
            <div class="pull-right" style="margin-right: 20px">
              <el-button   @click="" >新增联系人</el-button>
            </div>
          </div>
        </el-col>
      </el-col>
    </div>
    <div class="clearfix list-button">
      <div class="pull-left">
        <h4 class="list-button-text">商机描述：</h4>
      </div>
    </div>
    <div class="list-center-bottom" style="height: 140px;border: 1px solid #dfdfdf;background: #fafafa">
      <el-input type="textarea" ></el-input>
    </div>
  </div>
</template>
<script>
  import HTable from '@/components/layout/table/HTable'
  export default {
    name: 'chanceDetail',
    components: {
      HTable
    },
    data () {
      return {
        total: 0,
        page: 10,
        listLoading: false,
        tableData: [],
        ruleForm: {
          name: '',
          region: '',
          date1: ''
        },
        rules: {
          name: [
            { required: true, message: '请输入', trigger: 'blur' }
          ],
          region: [
            { required: true, message: '请选择', trigger: 'change' }
          ]
        },
        stateOptions: [{
          value: 'state1',
          label: '全部'
        }, {
          value: 'state2',
          label: '新线索'
        }, {
          value: 'state3',
          label: '跟进中'
        }, {
          value: 'state4',
          label: '已成单'
        }, {
          value: 'state5',
          label: '已终止'
        }],
        stateValue: ''
      }
    },
    watch: {
      /**
       * @params 监听参数变化重新获取数据
       * */
      params: {
        handler (val) {
          this.getData(val)
        },
        deep: true
      }
    },
    computed: {
      state () {
        return this.$store.state.app
      }
    },
    methods: {
      /**
       * @params:category 分类 page：页码 limit:条数
       * */
      getData (params) {
      },
      /**
       * @on-change 页码改变的回调，返回改变后的页码
       * */
      pageChange (page) {
        this.params.page = page
      },
      /**
       * @on-page-size-change 切换每页条数时的回调，返回切换后的每页条数
       * */
      PageSizeChange (limit) {
        this.params.limit = limit
      },
      /**
       * 表格对应操作方法
       * @show 查看
       * @remove 删除
       * @edit
       */
      remove (index) {
        this.listData.splice(index, 1)
      },
      edit (index) {
      },
      /**
       * 多选
       * selection：已选项数据 row：刚选择的项数据
       */
      onSelect (selection, row) {
        // console.log(selection,row)
      },
      onSelectionChange (selection) {
        this.selection = selection
      }
    },
    created () {
      this.getData(this.params)
    },
    mounted () {
    }
  }
</script>



